* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Montserrat, sans-serif;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.ukraine {
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  width: 356px;
  height: 474px;
  position: relative;
  box-shadow: 0 10px 10px #f5f5f71a;
}

.ukraine-tittle {
  color: #fff;
  letter-spacing: -.96px;
  margin: 0;
  padding-top: 26px;
  padding-left: 40px;
  font-family: DM Sans;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 28px;
}

.ukraine-svg {
  margin-left: 12px;
}

.ukraine-solider {
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  width: 159px;
  height: 292px;
  display: inline-flex;
}

.svg-btn {
  margin: 24px auto 0;
  display: block;
}

.logo__item {
  align-items: center;
  gap: 14px;
  height: 32px;
  display: flex;
}

.fundNumber {
  color: #fff;
}

.logo__img {
  display: flex;
}

.scroll-container {
  width: 159px;
  height: 292px;
  margin-top: 40px;
  margin-left: 40px;
  overflow: hidden;
}

.scroll-btn {
  margin: 20px auto 0;
  display: block;
}

.layout {
  padding-top: 40px;
  padding-bottom: 10px;
}

@media screen and (width >= 1200px) {
  .layout {
    grid-template-areas: "sidebar content";
    gap: 40px;
    display: grid;
  }
}

@media screen and (width <= 768px) {
  .books__item {
    margin-bottom: 40px;
  }
}

.home__category-container.container_active, .home-typeBook.container_active {
  display: block;
}

.category-item {
  color: #1119;
  cursor: pointer;
  transform-origin: 0;
  transform: scale(.97);
}

.category-item:not(:last-child) {
  margin-bottom: 24px;
}

@media screen and (width >= 768px) {
  .category-item:not(:last-child) {
    margin-bottom: 32px;
  }
}

@media screen and (width >= 1200px) {
  .category-item:not(:last-child) {
    margin-bottom: 24px;
  }
}

.category-item.active, .category-item:hover, .category-item:focus {
  text-transform: uppercase;
  color: #4f2ee8;
  font-weight: 700;
  transform: scale(1);
}

@media screen and (width >= 768px) {
  .books__card-author {
    max-width: 218px;
  }
}

@media screen and (width >= 1200px) {
  .books__card-author {
    max-width: 180px;
  }

  .category__item {
    flex-basis: calc(20% - 20px);
  }
}

.categories-list {
  width: 335px;
  height: 472px;
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.125;
  display: block;
  overflow: auto;
}

.categories-list::-webkit-scrollbar {
  width: 8px;
}

.categories-list::-webkit-scrollbar-track {
  background-color: #0000;
}

.categories-list::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 4px;
}

.scategories-list::-webkit-scrollbar-thumb:hover {
  background-color: #b4afaf;
}

.home-typeBook {
  max-width: 996px;
}

.home-heading {
  color: #111;
  letter-spacing: -1.92px;
  margin-bottom: 40px;
  font-family: DM Sans;
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: 52px;
}

.home-heading_custom {
  color: #4f2ee8;
}

.category-name {
  color: #b4afaf;
  letter-spacing: .42px;
  text-transform: uppercase;
  max-width: 390px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.category-books {
  flex-wrap: wrap;
  gap: 24px;
  display: flex;
}

.book-item {
  flex-flow: column wrap;
  gap: 4px;
  width: 180px;
  display: flex;
  overflow: hidden;
}

.book-item_image {
  background: #d3d3d3 50% / cover no-repeat;
  border-radius: 8px;
  height: 256px;
}

.book-item_title {
  color: #111;
  text-overflow: ellipsis;
  white-space: nowrap;
  letter-spacing: -.64px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  overflow: hidden;
}

.book-item_author {
  color: #b4afaf;
  text-overflow: ellipsis;
  white-space: nowrap;
  letter-spacing: -.48px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
  overflow: hidden;
}

.see-more-button {
  letter-spacing: -.01em;
  text-transform: uppercase;
  color: #111;
  background-color: #0000;
  border: 2px solid #4f2ee8;
  border-radius: 40px;
  margin-bottom: 40px;
  margin-left: auto;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  transition-property: background-color, color;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.see-more-button:hover, .see-more-button:focus {
  color: #fff;
  background-color: #4f2ee8;
}

a, ul {
  font-style: normal;
  text-decoration: none;
  list-style: none;
}

.logo {
  width: auto;
  height: 28px;
}

.main-header {
  z-index: 1;
  background: #fff;
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
}

.main-header__navigation {
  letter-spacing: .02em;
  justify-content: space-between;
  align-items: center;
  padding: 4px 0;
  font-weight: 500;
  display: flex;
}

@media screen and (width >= 1280px) {
  .main-header__navigation {
    padding: 5px 0;
  }
}

.main-header__menu {
  letter-spacing: .02em;
  justify-content: flex-start;
  align-items: center;
  font-size: 14px;
  line-height: 16px;
  display: flex;
}

.main-header__menu li:nth-of-type(2) {
  margin-left: 15px;
}

@media screen and (width >= 768px) {
  .main-header__menu li:nth-of-type(2) {
    margin-left: 60px;
  }
}

@media screen and (width >= 1280px) {
  .main-header__menu li:nth-of-type(2) {
    margin-left: 105px;
  }
}

.main-header__hamburger {
  background-color: #0000;
  border: none;
  padding-right: 25px;
  display: flex;
  position: absolute;
  right: 0;
}

.main-header__hamburger:hover, .main-header__hamburger:focus {
  fill: #00f;
}

@media screen and (width >= 768px) {
  .main-header__hamburger {
    display: none;
  }
}

.main-menu__nav {
  text-align: center;
  color: #111;
  letter-spacing: -.56px;
  text-transform: uppercase;
  border-radius: 36px;
  max-width: 254px;
  margin-right: 46px;
  padding: 8px 16px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
  transition: background-color .3s;
}

.main-menu__nav__hamburger {
  background-color: #0000;
  border: none;
  display: flex;
}

.main-menu__nav__hamburger:hover, .main-menu__nav__hamburger:focus {
  fill: #00f;
}

@media screen and (width >= 768px) {
  .main-menu__nav__hamburger {
    display: none;
  }
}

.main-menu__nav a {
  color: #111;
  display: none;
}

@media screen and (width >= 768px) {
  .main-menu__nav a {
    display: flex;
  }

  .main-menu__nav:nth-of-type(2):hover, .main-menu__nav:nth-of-type(3):hover {
    cursor: pointer;
    background-color: #eac645;
    border-radius: 36px;
    padding: 8px 16px;
    text-decoration: none;
    transition: background-color .3s;
  }
}

.main-menu__icon {
  margin-left: 5px;
}

.mode-buttons__login {
  letter-spacing: .02em;
  justify-content: flex-end;
  align-items: center;
  gap: 50px;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
  display: flex;
}

.switch {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: absolute;
  top: 25px;
  right: 85px;
}

@media screen and (width >= 768px) {
  .switch {
    width: 40px;
    height: 24px;
    display: inline-block;
    position: absolute;
    top: 28px;
    right: 230px;
  }
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  background-color: #fff;
  width: 18px;
  height: 18px;
  transition: all .4s;
  position: absolute;
  bottom: 3px;
  left: 3px;
}

input:checked + .slider {
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
}

input:focus + .slider {
  box-shadow: 0 0 1px linear-gradient(180deg, #4f2ee8 0%, #dcdcdc 100%);
}

input:checked + .slider:before {
  transform: translateX(18px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.sign-up {
  text-align: left;
  color: #fff;
  cursor: pointer;
  background: #4f2ee8;
  border: 1.5px solid #111;
  border-radius: 18px;
  width: 184px;
  height: 48px;
  padding-left: 14px;
  display: none;
}

@media screen and (width >= 768px) {
  .sign-up {
    display: flex;
    position: absolute;
    top: 13px;
    right: 15px;
  }
}

.sign-up__arrow {
  position: absolute;
  top: 12px;
  right: 25px;
}

.sign-up__text {
  position: absolute;
  top: 14px;
}

.profile {
  text-align: left;
  color: #fff;
  cursor: pointer;
  background: #4f2ee8;
  border: 1.5px solid;
  border-radius: 36px;
  width: 184px;
  height: 48px;
  padding-left: 14px;
  display: none;
}

.profile__down-arrow {
  margin-top: -4px;
  margin-left: 80px;
  position: absolute;
}

.menu {
  cursor: pointer;
  background-color: #0000;
  border: none;
  padding-right: 20px;
  display: flex;
  position: absolute;
  top: 10px;
  right: 0;
}

@media screen and (width >= 768px) {
  .menu {
    display: none;
  }
}

.line {
  fill: none;
  stroke: #000;
  stroke-width: 6px;
  transition: stroke-dasharray .6s cubic-bezier(.4, 0, .2, 1), stroke-dashoffset .6s cubic-bezier(.4, 0, .2, 1);
}

.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 6px;
}

.line2 {
  stroke-dasharray: 60 60;
  stroke-width: 6px;
}

.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 6px;
}

.opened .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134px;
  stroke-width: 6px;
}

.opened .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30px;
  stroke-width: 6px;
}

.opened .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134px;
  stroke-width: 6px;
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container {
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  width: auto;
  margin-top: 66px;
  transition: transform .25s;
  display: flex;
  position: fixed;
  inset: 0;
  transform: translateX(100%);
}

@media screen and (width >= 768px) {
  .menu-container {
    display: none;
  }
}

.mobile-menu {
  flex-direction: column;
  justify-content: space-between;
  margin-top: 48px;
  display: flex;
}

.mobile-menu_nav {
  flex-direction: column;
  gap: 36px;
  display: flex;
}

.mobile-menu_nav-item {
  color: #fff;
  letter-spacing: -.56px;
  text-transform: uppercase;
  z-index: 0;
  text-align: center;
  justify-content: center;
  margin-top: 160px;
  margin-left: 100px;
  padding: 8px 16px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  display: flex;
  position: absolute;
}

.mobile-menu a:hover, .mobile-menu a:focus, .mobile-menu a:active {
  color: #111;
  fill: #111;
  background-color: #eac645;
  border-radius: 36px;
  padding: 8px 16px;
  text-decoration: none;
  transition: background-color .3s;
}

.mobile-menu__icon {
  fill: #fff;
