  background: #fff;
  border: 1.5px solid #111;
  border-radius: 18px;
  margin: 0 auto;
  padding: 15px 24px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  display: flex;
  position: absolute;
  bottom: 40px;
  left: 30px;
}

.log-out__arrow {
  margin-left: 5px;
}

.shopping-container {
  padding-top: 40px;
